/** @define SwatchPicker */

.SwatchPicker {
  @apply(--layout-horizontal);
  @apply(--layout-center);

  min-height: 48px;

  @nest &-button {
    --paper-swatch-picker-color-size: 48px;
    --paper-icon-button-ink-color: var(--accent-color);

    color: var(--accent-color);
  }

  @nest &-icon {
    margin-right: 24px;
  }
}
